<template>
    <div class="task-list">
      <Task v-for="task in tasks" :key="task.id" :task="task" />
    </div>
  </template>
  
  <script>
  import Task from './TaskItem.vue';
  import axios from 'axios'; // Import for API requests
  
  export default {
    components: {
      Task,
    },
    data() {
      return {
        tasks: [],
      };
    },
    created() {
      this.fetchTasks();
    },
    methods: {
      async fetchTasks() {
        const response = await axios.get('http://localhost:8000/api/tasks');
        this.tasks = response.data;
      },
    },
  };
  </script>
  
  <style scoped>
.task-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
</style>
  ./TaskItem.vue