<template>
    <div class="add-task">
      <h2>Add Task</h2>
      <form @submit.prevent="handleSubmit">
        <label for="title">Title:</label>
        <input type="text" id="title" v-model="newTask.title" required>
        <button type="submit">Add Task</button>
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        newTask: {
          title: '',
        },
      };
    },
    methods: {
      async handleSubmit() {
        await axios.post('http://localhost:8000/api/tasks', this.newTask);
        this.newTask.title = ''; // Clear form after submission
        this.$emit('taskAdded'); // Emit event to update task list
      },
    },
  };
  </script>
  
  <style scoped>
  .add-task {
    border: 1px solid #eee;
    padding: 15px;
    margin-bottom: 15px;
  }
  
  .add-task form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .add-task label {
    font-weight: bold;
  }
  
  .add-task input {
    border: 1px solid #ddd;
    padding: 5px;
  }
  
  .add-task button {
    background-color: #007bff; /* Blue */
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
  }
  </style>
  